import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  HorizontalScroll
} from "@vkontakte/vkui";

import { ShopItem, MiningItem, StickersItem } from "../items";

import { requestMethod, formatCoins, dataShopLoc, dataLootBox, dataStickers, dataMining } from "../helpers";

const Shop = ({
  id,
  openInfo,
  transfer,
  buyItem,
  scheme,
  goHome,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  snackbar,
  pornCoins,
  miners,
  openBuyMining,
  openInfoLootBox,
  openInfoStickers,
}) => {
  const [activeTab, setActiveTab] = useState("product");
  // const [localScheme, setLocalScheme] = useState("bright_light");

  // useEffect(() => {
  //   console.log("scheme", scheme);
  //   if (scheme) {
  //     setLocalScheme(scheme);
  //     console.log("localScheme", localScheme);

  //   }
  // }, [scheme]);

  function tabProduct() {
    setActiveTab("product");
    window.scrollTo(0, 0);
  }

  function tabMining() {
    setActiveTab("mining");
    window.scrollTo(0, 0);
  }

  function tabStickers() {
    setActiveTab("stickers");
    window.scrollTo(0, 0);
  }

  function tabLootBox() {
    setActiveTab("loot-box");
    window.scrollTo(0, 0);
  }

  const renderShop = dataShopLoc.map((item, index) => {
    return (
      <ShopItem
        key={index}
        id={item.id}
        name={item.name}
        price={item.price}
        icon={item.icon}
        openInfo={openInfo}
        openBuyItem={openBuyItem}
        scheme={scheme}
      />
    );
  });

  const renderMining = dataMining.map((item, index) => {
    let currentLevelItem = item.levels[miners[item.id]];
    let localBuyItem = buyItem;

    if (miners[item.id] >= 5) {
      currentLevelItem = item.levels[miners[item.id] - 1];
      // currentLevelItem = "–ú–∞–∫—Å–∏–º–∞–ª—å–Ω—ã–π —É—Ä–æ–≤–µ–Ω—å";
      localBuyItem = () => {};
    }

    let price = currentLevelItem.price / 100000;

    return (
      <MiningItem
        key={index}
        id={item.id}
        name={item.name}
        price={price}
        icon={item.icon}
        cps={currentLevelItem.cps}
        scheme={scheme}
        buyItem={localBuyItem}
        level={miners[item.id]}
        openBuyMining={openBuyMining}
      />
    );
  });


  const renderStickers = dataStickers.map((item, index) => {
    return (
      <StickersItem
        key={index}
        id={item.id}
        name={item.name}
        price={item.price}
        icon={item.icon}
        amount={item.amount}
        openInfoStickers={openInfoStickers}
        openBuyMining={openBuyMining}
        openBuyItem={openBuyItem}
        scheme={scheme}
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={<PanelHeaderBack onClick={goHome} data-to="home" />}
      >
        –ú–∞–≥–∞–∑–∏–Ω
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "–í—ã –ø–æ–¥–∫–ª—é—á–∏–ª–∏—Å—å —Å 2 —É—Å—Ç—Ä–æ–π—Å—Ç–≤ ",
          socketReconnect,
          "–ü–µ—Ä–µ–ø–æ–¥–∫–ª—é—á–∏—Ç—å—Å—è"
        )
      ) : (
        <>
          <FixedLayout vertical="top" filled={true}>
            <Tabs>
            <HorizontalScroll>
              <TabsItem onClick={tabProduct} selected={activeTab === "product"}>
                –¢–æ–≤–∞—Ä—ã
              </TabsItem>
              <TabsItem onClick={tabMining} selected={activeTab === "mining"}>
                –ú–∞–π–Ω–µ—Ä—ã
              </TabsItem>
              <TabsItem
                onClick={tabStickers}
                selected={activeTab === "stickers"}
              >
                –°—Ç–∏–∫–µ—Ä—ã
              </TabsItem>
              
              </HorizontalScroll>
            </Tabs>
          </FixedLayout>

          {networkStatus ? (
            stub(
              "–û—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç –∏–Ω—Ç–µ—Ä–Ω–µ—Ç - —Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ!",
              updateConnection,
              "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "–û—à–∏–±–∫–∞ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è –∫ —Å–µ—Ä–≤–µ—Ä—É",
                  socketReconnect,
                  "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å"
                )
              ) : (
                <>
                  {activeTab === "product" ? (
                    <>
                      {renderShop.length ? (
                        <Div style={{ marginTop: 55 }}>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              marginLeft: 25,
                              marginBottom: 10,
                            }}
                          >
                            –ë–∞–ª–∞–Ω—Å: {formatCoins(pornCoins)}
                            <Div className="iconPornCoin16px" style={{ marginLeft: 8 }} />
                          </Div>

                          {renderShop}
                        </Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "mining" ? (
                    <>
                      {renderMining.length ? (
                        <Div style={{ marginTop: 55 }}>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              marginLeft: 25,
                              marginBottom: 10,
                            }}
                          >
                            –ë–∞–ª–∞–Ω—Å: {formatCoins(pornCoins)}
                            <Div className="iconPornCoin16px" style={{ marginLeft: 8 }} />
                          </Div>

                          {renderMining}
                        </Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}


                  {activeTab === "stickers" ? (
                    <>
                      {renderStickers.length ? (
                        <Div style={{ marginTop: 55 }}>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              marginLeft: 25,
                              marginBottom: 10,
                            }}
                          >
                            –ë–∞–ª–∞–Ω—Å: {formatCoins(pornCoins)}
                            <Div className="iconPornCoin16px" style={{ marginLeft: 8 }} />
                          </Div>

                          {renderStickers}
                        </Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}
                </>
              )}
              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Shop };
