export const formatCoinsToServer = (input) => {
    input = String(input);
    let result = 0;

    if (/(,|\.)/g.test(input)) {
        let [a, b] = input.split(/,|\./g);

        if (b.length < 5) {
            b = b + '0'.repeat(5 - b.length);
        }
        
        if (b.length > 5) {
            b = b.slice(0, 5);
        }

        result = Number(`${a}${b}`);
    } else {
        result = Number(input + '000');
    }

    return result;
};