import React from "react";
import { Cell, Div, Link, Button } from "@vkontakte/vkui";
import Icon16Like from "@vkontakte/icons/dist/16/like";

import { formatCoins } from "../helpers";

const MiningInventoryItem = ({
  id,
  name,
  color,
  price,
  icon,
  scheme,
  cps,
  count,
  buyItem,
  level,
}) => {
  return (
    <Div className={scheme === "bright_light" ? "cardBlack" : "cardWhite"}>
      <Cell
        className="myItem"
        before={icon}
        asideContent={
          <Div
            style={{
              color: `${scheme === "bright_light" ? "black" :  "white"}`,
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
            }}
          >
            {formatCoins(cps)}
            <Div className="iconPornCoin16px" />
            <Div style={{ margin: 0, padding: 0 }}>/—Å–µ–∫</Div>
          </Div>
        }
        description={
            <Div>–£—Ä–æ–≤–µ–Ω—å: {level}</Div>
        }
      >
        <Div
          style={{
            fontSize: 14,
            userSelect: "none",
          }}
        >
          {name}
        </Div>
      </Cell>
    </Div>
  );
};

export { MiningInventoryItem };
