import React from "react";
import {
  ModalRoot,
  ModalCard,
  InfoRow,
  FormLayout,
  Input,
} from "@vkontakte/vkui";

const ModalShopInfoItem = ({
  iconItem,
  nameItem,
  ratingItem,
  timeItem,
  targetIdShop,
  setActiveModal,
}) => {
  return (
    <ModalRoot activeModal="info">
      <ModalCard
        id="info"
        onClose={() => setActiveModal(null)}
        icon={iconItem}
        header={nameItem}
        caption={
          <>
            {targetIdShop === 1 ? (
              <InfoRow>
                {nameItem} - –ø—Ä–∏ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–∏ —Å—Ç–∞–≤–∏—Ç +{ratingItem} Like.
              </InfoRow>
            ) : (
              <InfoRow>
                {nameItem} - –ø—Ä–∏ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–∏ —Å—Ç–∞–≤–∏—Ç -{ratingItem} Dislike.
              </InfoRow>
            )}
          </>
        }
      ></ModalCard>
    </ModalRoot>
  );
};

export { ModalShopInfoItem };
