import React from "react";
import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import Div from "@vkontakte/vkui/dist/components/Div/Div";
import Link from "@vkontakte/vkui/dist/components/Link/Link";

import { formatCoins } from "../helpers";

const StickersItem = ({
  id,
  name,
  price,
  icon,
  openInfoStickers,
  openBuyItem,
  scheme,
  buyItem,
  openBuyMining
}) => {
  return (
    <Div
    className={
      scheme === "bright_light"
        ? "itemShop_bright_light"
        : "itemShop_space_gray"
    }
    // className="itemShop_bright_light"
    >
      <Cell
        style={{width: "100%"}}
        before={icon}
        asideContent={
          <Link id={id} onClick={openBuyMining} mode="tertiary">
            –∫—É–ø–∏—Ç—å
          </Link>
        }
        description={
          <Div>
            <Div
              className="flexStartRowCenter"
            >
              {`${formatCoins(price)}`}
              <Div className="iconPornCoin16px" style={{ marginLeft: 8 }} />
            </Div>
            <Div style={{marginTop: "3px"}}>
              <Link id={id} onClick={openInfoStickers}>
                –ü–æ–¥—Ä–æ–±–Ω–µ–µ
              </Link>
            </Div>
          </Div>
        }
      >
        {name}
      </Cell>
    </Div>
  );
};

export { StickersItem };
