import React from "react";
import { Cell, Div, Link } from "@vkontakte/vkui";
import Icon16Like from "@vkontakte/icons/dist/16/like";

import { formatCoins } from "../helpers";

const ShopItem = ({ id, name, price, icon, openInfo, openBuyItem, scheme }) => {
  return (
    <Div
      className={
        scheme === "bright_light"
          ? "itemShop_bright_light"
          : "itemShop_space_gray"
      }
    >
      <Cell
        style={{ width: "100%" }}
        before={
          id === 1 
          ?  
        <Icon16Like fill="red" width={28} height={28}/>
        :
        <Icon16Like fill="#E48900" width={28} height={28}/>
      
      }
        asideContent={
          <Link id={id} onClick={openBuyItem} mode="tertiary">
            –∫—É–ø–∏—Ç—å
          </Link>
        }
        description={
          <Div>
            <Div className="flexStartRowCenter">
              {`${formatCoins(price)}`}
              <Div className="iconPornCoin16px" style={{ marginLeft: 8 }} />
            </Div>
            <Div style={{marginTop: "3px"}}>
              <Link id={id} onClick={openInfo} >
                –ü–æ–¥—Ä–æ–±–Ω–µ–µ
              </Link>
            </Div>
          </Div>
        }
      >
        {name}
      </Cell>
    </Div>
  );
};

export { ShopItem };
