import React from "react";
import { Cell, Div, Link } from "@vkontakte/vkui";
import "../styles/components.sass";

const InventoryItem = ({
  id,
  name,
  icon,
  openActionSheetUseItem,
  count,
  targetIdInventory,
  scheme,
}) => {
  return (
    <Div className={scheme === "bright_light" ? "cardBlack" : "cardWhite"}>
      <Cell
        className="myItem"
        before={icon}
        asideContent={
          <Div id={id} mode="tertiary">
            {count} —à—Ç
          </Div>
        }
        description={
          <Div 
          style={{
            display: "flex",
            alignItems: "center",
            flexDirection: "column",
            justifyContent: "center",
            backgroundColor: `${
              scheme === "bright_light" ? "#010101" :   "white"
            }`,
            color: scheme === "bright_light" ? "white" :  "#010101",
            borderRadius: "7px",
            userSelect: "none",
            height: "30px",
            width: "90px",
            fontSize: "10px"
          }}
          id={id} 
          onClick={openActionSheetUseItem}
          >
            –ò—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å
          </Div>
        }
      >
        {name}
      </Cell>
    </Div>
  );
};

export { InventoryItem };
