import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
} from "@vkontakte/vkui";

import { InventoryItem, MiningInventoryItem } from "../items";
// import dataMining from "../helpers/dataMining";
import { requestMethod, formatCoins, dataShop, dataLootBox, dataStickers, dataMining } from "../helpers";


const Inventory = ({
  id,
  dataInventory,
  targetIdInventory,
  openUseItem,
  scheme,
  goHome,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  snackbar,
  dataMiners,
  miners,
  openActionSheetUseItem
}) => {
  const [activeTab, setActiveTab] = useState("inventory");

  function tabInventory() {
    setActiveTab("inventory");
    window.scrollTo(0, 0);
  }

  function tabMaining() {
    setActiveTab("maining");
    window.scrollTo(0, 0);
  }


  const renderInventory = dataInventory.map((item, index) => {
    return (
      <InventoryItem
        key={index}
        id={item.id}
        name={item.name}
        icon={item.icon}
        count={item.count}
        openBuyItem={openBuyItem}
        targetIdInventory={targetIdInventory}
        scheme={scheme}
        openActionSheetUseItem={openActionSheetUseItem}
      />
    );
  });

  const renderMaining = dataMiners.map((item, index) => {
    const currentItem = dataMining.find(($item) => $item.id === item.id).levels[
      item.level - 1
    ];

    return (
      <MiningInventoryItem
        key={index}
        id={item.id}
        name={item.name}
        icon={item.icon}
        cps={currentItem.cps}
        scheme={scheme}
        level={item.level}
        
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={<PanelHeaderBack onClick={goHome} data-to="home" />}
      >
        –ò–Ω–≤–µ–Ω—Ç–∞—Ä—å
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "–í—ã –ø–æ–¥–∫–ª—é—á–∏–ª–∏—Å—å —Å 2 —É—Å—Ç—Ä–æ–π—Å—Ç–≤ ",
          socketReconnect,
          "–ü–µ—Ä–µ–ø–æ–¥–∫–ª—é—á–∏—Ç—å—Å—è"
        )
      ) : (
        <>
          <FixedLayout vertical="top" filled={true}>
            <Tabs>
              <TabsItem
                onClick={tabInventory}
                selected={activeTab === "inventory"}
              >
                –ò–º—É—â–µ—Å—Ç–≤–æ
              </TabsItem>
              <TabsItem onClick={tabMaining} selected={activeTab === "maining"}>
                –ú–∞–π–Ω–µ—Ä—ã
              </TabsItem>
            </Tabs>
          </FixedLayout>

          {networkStatus ? (
            stub(
              "–û—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç –∏–Ω—Ç–µ—Ä–Ω–µ—Ç - —Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ!",
              updateConnection,
              "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "–û—à–∏–±–∫–∞ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è –∫ —Å–µ—Ä–≤–µ—Ä—É",
                  socketReconnect,
                  "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å"
                )
              ) : (
                <>
                  {activeTab === "inventory" ? (
                    <>
                      {
                        renderInventory.length ? (
                          <Div style={{ marginTop: 55 }}>{renderInventory}</Div>
                        ) : (
                          <Div className="flexCenterRowMarginHeader">
                            –ü–æ–∫–∞ —É –í–∞—Å –Ω–µ—Ç –∏–º—É—â–µ—Å—Ç–≤–∞, —á—Ç–æ–±—ã –∫—É–ø–∏—Ç—å –ø–µ—Ä–µ–π–¥–∏—Ç–µ –≤
                            –º–∞–≥–∞–∑–∏–Ω.
                          </Div>
                        )
                      }
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "maining" ? (
                    <>
                      {renderMaining.length ? (
                        <Div style={{ marginTop: 55 }}>{renderMaining}</Div>
                      ) : (
                        <Div className="flexCenterRowMarginHeader">
                          –ü–æ–∫–∞ —É –í–∞—Å –Ω–µ—Ç—É –º–∞–π–Ω–µ—Ä–æ–≤, —á—Ç–æ–±—ã –∫—É–ø–∏—Ç—å –ø–µ—Ä–µ–π–¥–∏—Ç–µ –≤
                          –º–∞–≥–∞–∑–∏–Ω.
                        </Div>
                      )}
                    </>
                  ) : (
                    ""
                  )}
                </>
              )}

              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Inventory };
