import React from 'react';
import { ModalRoot, ModalCard, FormLayout, Input,} from '@vkontakte/vkui';

const ModalInventoryUseItem = ({targetIdInventory, iconItem, nameItem, setActiveModal, onChangeHandlerId, $useItem}) => {


    return (
      <ModalRoot activeModal="use-item">
          <ModalCard
            id="use-item"
            onClose={() => setActiveModal(null)}
            icon={iconItem}
            header={nameItem}
            caption={
              <>
                <FormLayout>
                  <Input
                    top="–£–∫–∞–∂–∏—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è"
                    placeholder="https://vk.com/porncoin"
                    onChange={onChangeHandlerId}
                    type="text"
                  />
                </FormLayout>
              </>
            }
            actions={[
              {
                title: "–ò—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å",
                mode: "commerce",
                action: () => {
                  $useItem(targetIdInventory);
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
    );
};

export { ModalInventoryUseItem };

