import React from "react";
import { Cell, Div, Link, Button } from "@vkontakte/vkui";

import { formatCoins } from "../helpers";

const MiningItem = ({
  id,
  name,
  color,
  price,
  icon,
  scheme,
  cps,
  count,
  buyItem,
  level,
  openBuyMining
}) => {
  return (
    <Div 
     className={
        scheme === "bright_light"
          ? "itemShop_bright_light"
          : "itemShop_space_gray"
      }
    >

      <Cell
        style={{width: "100%"}}
        before={icon}
        description={
          <Div
            id={id}
            onClick={openBuyMining}
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
              justifyContent: "center",
              marginBottom: 5,
              backgroundColor: `${
                scheme === "bright_light" ? "#010101" :   "white"
              }`,
              borderRadius: 5,
              height: "30px",
              color: `${scheme === "bright_light" ? "white" :  "#010101"}`
            }}
          >
            {level < 5 ? (
              <>
                <Div
                  style={{
                    userSelect: "none",
                  }}
                >
                  –ö—É–ø–∏—Ç—å –∑–∞ {price}
                </Div>
                <Div className="iconPornCoin16px" style={{ marginLeft: 8 }} />
              </>
            ) : (
              <>
                <Div
                  style={{
                    userSelect: "none",
                  }}
                >
                  –ú–∞–∫—Å–∏–º–∞–ª—å–Ω—ã–π —É—Ä–æ–≤–µ–Ω—å
                </Div>
              </>
            )}


          </Div>
        }
      >
        <Div
          style={{
            fontSize: 14,
            marginBottom: 5,
            userSelect: "none",
          }}
        >
          <Div
            style={{
              color: `${scheme === "bright_light" ? "black" :  "white"}`,
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              flexDirection: "row",
            }}
          >
            <Div>{name}</Div>

            <Div
              style={{
                color: `${scheme === "bright_light" ? "black" :  "white"}`,
                display: "flex",
                alignItems: "center",
                flexDirection: "row",
              }}
            >
              {formatCoins(cps)}
              <Div className="iconPornCoin16px" />
              <Div style={{ margin: 0, padding: 0 }}>/—Å–µ–∫</Div>
            </Div>
          </Div>
        </Div>
      </Cell>
    </Div>
  );
};

export { MiningItem };
