import React from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import Icon12Fire from "@vkontakte/icons/dist/12/fire";
import Icon16LikeOutline from "@vkontakte/icons/dist/16/like_outline";
import Icon16Like from "@vkontakte/icons/dist/16/like";
import { getRankByRating, formatCoins } from "../helpers";

const TopUsersItem = ({
  id,
  firstName,
  lastName,
  rating,
  photoURL,
  amount,
  idVk,
  ranks,
  isVIP,
  vipNameColor,
  likes,
  dislikes,
}) => {
  const urlUser = "https://vk.com/id" + idVk;

  return (
    <Div className="itemMainTopUsers itemMain">
      <span style={{ paddingLeft: "0px" }}>{id}</span>

      <Cell
        before={
          photoURL ? (
            <Link href={urlUser} target="_blank">
              <Avatar src={photoURL} />
            </Link>
          ) : (
            <Avatar src={"https://vk.com/images/camera_200.png"} />
          )
        }
        description={
          <Div className="itemMainTopUsersDescription">
            <Div className="itemMainTopUsersLike">
              <Div className="iconPornCoin16px" />
              <Div style={{ whiteSpace: "nowrap" }}>{formatCoins(amount)}</Div>
            </Div>
            <Div className="itemMainTopUsersLike">
              <Icon16Like fill="red" />
              <Div
                style={{
                  marginLeft: "5px",
                  marginRight: "5px",
                  marginTop: "2px",
                }}
              >
                +{likes}
              </Div>
              <Icon16Like fill="#E48900" />
              <Div style={{ marginLeft: "5px", marginTop: "2px" }}>
                -{dislikes}
              </Div>
            </Div>
          </Div>
        }
      >
        <span
          style={{
            color: [, "green", "#D40000", "blue", "#800080"][vipNameColor],
            marginLeft: "10px",
          }}
        >
          {firstName + " " + lastName}
        </span>
      </Cell>
    </Div>
  );
};

export { TopUsersItem };
