export class SocketManager {
    socket = null;

    static createConnection() {
        if (SocketManager.socket) {
            return;
        }
        
        SocketManager.socket = io("https://one-digitalspace.ru", {
            path: "/porncoin/socket",
            query: document.location.search.slice(1),
            reconnectionAttempts: 1,
            transports: ['websocket']
        });
        
        return SocketManager;
    }
    
    /**
     * @param {string} methodName - –ò–º—è –º–µ—Ç–æ–¥–∞
     * @param {object} params - –ü–∞—Ä–∞–º–µ—Ç—Ä—ã –º–µ—Ç–æ–¥–∞
     * @returns {Promise<Answer>}
     */
    static async callMethod(methodName, params = {}) {
        const { socket } = SocketManager;

        if (!socket)
            return Promise.resolve({
                status: 'denied',
                message: '–°–æ–∫–µ—Ç –Ω–µ –ø–æ–¥–∫–ª—é—á–µ–Ω'
            });

        return new Promise((resolve) => {
            socket.emit(methodName, params, (answer) => {
                return resolve(answer);
            });
        });
    }
}