import React, { useState, useEffect } from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import Icon16Down from "@vkontakte/icons/dist/16/down";
import Icon16Up from "@vkontakte/icons/dist/16/up";
import { formatCoins } from '../helpers';

const OperationsHistoryItem = ({
  senderId,
  targetId,
  amount,
  createdAt,
  formatDate,
  fetchedUser,
  senderName,
  targetName,
  senderPhoto,
  targetPhoto,
}) => {
  const photoURLTargetId = "https://vk.com/id" + targetId;
  const photoURLSenderId = "https://vk.com/id" + senderId;
  const idVkTargetId = "https://vk.com/id" + targetId;
  let now = new Date(createdAt);

  return (
    <>
      {senderId === fetchedUser.id ? (
        
          <Cell
            className="itemMain"
            before={
              fetchedUser.photo_200 ? (
                <Link
                  href={photoURLTargetId}
                  target="_blank"
                  style={{ userSelect: "none" }}
                >
                  <Avatar src={targetPhoto} />
                </Link>
              ) : (
                <Avatar src={"https://vk.com/images/camera_200.png"} />
              )
            }
            description={
              <Div style={{ marginLeft: 20 }}>
                <Div>
                  {fetchedUser && (
                    <Div
                      style={{
                        wordWrap: "break-word",
                        marginBottom: 5,
                        userSelect: "none",
                      }}
                    >
                      {targetName}
                    </Div>
                  )}
                </Div>

                <Div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    flexDirection: "row",
                    justifyContent: "start",
                  }}
                >
                  <Div style={{ color: "red" }}>- {formatCoins(amount)} </Div>
                  <Div className="iconPornCoin16px" style={{ marginLeft: 8 }} />
                </Div>
              </Div>
            }
            asideContent={
              <Div
                style={{
                  marginTop: "-16px",
                  display: "flex",
                  justifyContent: "end",
                  flexDirection: "row",
                  alignItems: "end",
                  fontSize: "10px",
                }}
              >
                {formatDate(createdAt)}
              </Div>
            }
          >
            <Div
              style={{
                marginBottom: "5px",
                paddingLeft: "15px",
                display: "flex",
                justifyContent: "start",
                flexDirection: "row",
                alignItems: "center",
                fontSize: "16px",
              }}
            >
              <Div>
                <Icon16Up fill="red" />
              </Div>
              <Div style={{ marginTop: 4, marginLeft: 4, userSelect: "none" }}>
                –ü–µ—Ä–µ–≤–æ–¥
              </Div>
            </Div>
          </Cell>
      ) : (
       
          <Cell
          className="itemMain"
            before={
              senderPhoto ? (
                <Link
                  href={photoURLSenderId}
                  target="_blank"
                  style={{ userSelect: "none" }}
                >
                  <Avatar src={senderPhoto} />
                </Link>
              ) : (
                <Avatar src={"https://vk.com/images/camera_200.png"} />
              )
            }
            description={
              <Div
                style={{
                  marginLeft: 20,
                }}
              >
                <Div>
                  <Div
                    style={{
                      wordWrap: "break-word",
                      marginBottom: 5,
                      userSelect: "none",
                    }}
                  >
                    {senderName}
                  </Div>
                </Div>
                <Div className="itemHistoryAmount">
                  <Div style={{ color: "green" }}>+ {formatCoins(amount)}</Div>
                  <Div className="iconPornCoin16px" style={{ marginLeft: 8 }} />
                </Div>
              </Div>
            }
            asideContent={
              <Div className="itemHistoryFormatDate">
                {formatDate(createdAt)}
              </Div>
            }
          >
            <Div className="itemHistoryType">
              <Div>
                <Icon16Down fill="green" />
              </Div>
              <Div style={{ marginTop: 4, marginLeft: 4, userSelect: "none" }}>
                –ü–æ–ø–æ–ª–Ω–µ–Ω–∏–µ
              </Div>
            </Div>
          </Cell>
      )}
    </>
  );
};

export { OperationsHistoryItem };
